<template>
  <div class="main-container">
    <div class="query-wrap" v-if="!showResult">
      <el-select v-model="district" placeholder="城区">
        <el-option
          v-for="item in districtOption"
          :key="item.key"
          :value="item.key"
          :label="item.district"
        ></el-option>
      </el-select>
      <el-select v-model="price" placeholder="价格区间">
        <el-option v-for="(item, index) in select" :key="index" :value="item"></el-option>
      </el-select>
      <el-select v-model="area" placeholder="面积区间">
        <el-option v-for="(item, index) in select" :key="index" :value="item"></el-option>
      </el-select>
      <el-select v-model="type" placeholder="类型">
        <el-option v-for="(item, index) in select" :key="index" :value="item"></el-option>
      </el-select>
      <div style="margin-top: 200px;">
        <el-button type="primary" @click="query">搜索</el-button>
      </div>
    </div>
    <single-item v-if="showResult"></single-item>
  </div>
</template>

<script>
import singleItem from "./singleItem";
export default {
  components: { singleItem },
  data() {
    return {
      showResult: false,
      district: null,
      region: "",
      price: "",
      area: "",
      type: "",
      select: [1, 2, 3, 4, 5, 6],
      districtOption: [
        { key: 1, district: "西湖" },
        { key: 2, district: "下城" },
        { key: 3, district: "江干" },
        { key: 4, district: "拱墅" },
        { key: 5, district: "上城" },
        { key: 6, district: "滨江" },
        { key: 7, district: "余杭" },
        { key: 8, district: "萧山" },
        { key: 9, district: "富阳" },
        { key: 10, district: "大江东" },
        { key: 11, district: "钱塘新区" }
      ],
      houseInfo: {}
    };
  },
  methods: {
    query() {
      let searchInfo = {
        district: this.district
      };
      this.$api
        .search(this.$store.getters.currentUser, searchInfo)
        .then(res => {
          console.log(res);
          this.houseInfo = res.data.data.houseItem;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.main-container {
  position: relative;
  margin: 0 auto;
  width: 1300px;
}
.query-wrap {
  margin-top: 300px;
}
</style>



