<template>
  <div class="main-container">
    <el-carousel height="600px" class="carousel-wrap">
      <el-carousel-item v-for="item in 4" :key="item">
        <h3>{{item}}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  data() {
    return {
      houseInfo: [
        {
          roomId: 1,
          title: "标题",
          district: null,
          districtCN: "西湖区",
          area: 10,
          price: 10000,
          layout: "四室三厅",
          orient: "朝南",
          tags: ["近地铁", "朝南"],
          roomFloor: null
        }
      ]
    };
  }
};
</script>

<style scoped>
/* .test {
  background-image: url(../assets/house2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
} */
.main-container {
  position: relative;
  margin: 0 auto;
  /* width: 800px; */
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>



