<template>
  <div class="main-container">
    <el-container class="single-info">
      <el-header height="auto" class="house-title">{{houseInfo.title}}</el-header>
      <el-main class="main-wrap">
        <div>
          <span class="house-price">
            <em>{{houseInfo.price}}</em>元/月
          </span>
          <span class="house-floor">{{houseInfo.roomFloor}}</span>
        </div>
        <div class="house-tags">
          <!-- <span class="tags-item" v-for="(item, index) in houseInfo.tags" :key="index">{{item}}</span> -->
          <span class="tags-item">{{houseInfo.tags}}</span>
        </div>
      </el-main>
      <el-footer class="msg-wrap" height="auto">
        <span class="msg-list">
          <img class="msg-img" src="@/assets/house@2x.png">
          {{houseInfo.districtCN}}
        </span>
        <span class="msg-list">
          <img class="msg-img" src="@/assets/area@2x.png">
          {{houseInfo.area}}㎡
        </span>
        <span class="msg-list">
          <img class="msg-img" src="@/assets/orient@2x.png">
          {{houseInfo.orient}}
        </span>
        <span class="msg-list">
          <img class="msg-img" src="@/assets/typ@2x.png">
          {{houseInfo.layout}}
        </span>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      houseInfo: {
        roomId: 1,
        title: "合租·中大文锦苑 5居室 北卧",
        district: null,
        districtCN: "西湖区",
        area: 8,
        price: 1730,
        layout: "5室1厅2卫",
        orient: "朝北",
        tags: "公寓|近地铁|押一付一|随时看房",
        roomFloor: "楼层：3/11层"
      }
    };
  },
  mounted() {
    this.$api
      .detail(this.$store.getters.currentUser, this.$route.params.roomId)
      .then(res => {
        console.log(res);
        this.houseInfo = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style scoped>
.main-container {
  position: relative;
  margin: 0 auto;
  width: 800px;
}
.single-info {
  position: relative;
  width: 100%;
  padding: 20px 50px;
  margin-top: 20px;
  text-align: start;
  /* box-sizing: border-box; */
  border: 1px solid #dcdfe6;
  border-radius: 5px;
}
.house-title {
  height: auto;
  margin: 20px;
  padding: 0;
  font-size: 45px;
  font-weight: 700;
  color: #101d37;
}
.house-title a {
  text-decoration: none;
  font-size: 45px;
  font-weight: 700;
  color: #101d37;
}
.house-title a:hover {
  color: #00ae66;
}
.house-price {
  color: #fa5741;
  font-size: 18px;
  font-weight: 700;
}
.house-floor {
  margin-left: 30px;
  font-size: 18px;
  font-weight: 700;
  color: #9399a5;
}
.house-tags {
  margin-top: 20px;
}
.tags-item {
  display: inline-block;
  background-color: rgba(132, 154, 174, 0.1);
  line-height: 30px;
  height: 30px;
  /* width: 70px; */
  margin-right: 12px;
  text-align: center;
  color: #849aae;
}
.msg-wrap {
  font-size: 18px;
  font-weight: 700;
  color: #9399a5;
}
.msg-list {
  display: inline-block;
  margin-right: 20px;
  text-align: center;
}
.msg-list img {
  display: block;
  margin: 0 auto;
  width: 50px;
  height: 50px;
}
.house-price {
  color: #fa5741;
  font-size: 18px;
  font-weight: 700;
}
.house-price em {
  font-size: 30px;
}
</style>



