<template>
  <div class="main-container">
    <el-container class="single-info" v-for="item in houseInfo" :key="item.roomId">
      <el-header height="auto" class="house-title">
        <router-link :to="{ path: '/Detail/' + item.roomId}">{{item.title}}</router-link>
      </el-header>
      <el-main class="msg-wrap">
        <span>{{item.districtCN}}</span>
        <i>/</i>
        <span>{{item.area}}㎡</span>
        <i>/</i>
        <span>{{item.orient}}</span>
        <i>/</i>
        <span>{{item.layout}}</span>
      </el-main>
      <el-footer class="house-price" height="auto">
        <span>
          <em>{{item.price}}</em>元/月
        </span>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      houseInfo: [
        {
          roomId: 1,
          title: "合租·中大文锦苑 5居室 北卧",
          district: null,
          districtCN: "西湖区",
          area: 8,
          price: 1730,
          layout: "5室1厅2卫",
          orient: "朝北",
          tags: "公寓|近地铁|押一付一|随时看房",
          roomFloor: null
        },
        {
          roomId: 2,
          title: "合租·山水人家白沙岛 4居室 南卧",
          district: null,
          districtCN: "西湖区",
          area: 18,
          price: 2490,
          layout: "4室1厅2卫",
          orient: "朝南",
          tags: "公寓|押一付一|随时看房",
          roomFloor: null
        }
      ]
    };
  },
  mounted() {
    this.$api
      .history(this.$store.getters.currentUser)
      .then(res => {
        console.log(res);
        this.houseInfo = res.data.data.historyItems;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style scoped>
.main-container {
  position: relative;
  text-align: start;
  margin: 0 auto;
  width: 800px;
}
.main-container h1 {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  margin: 0;
  font-size: 45px;
}
.single-info {
  position: relative;
  height: 300px;
  width: 100%;
  padding: 20px 50px;
  margin-top: 20px;
  /* box-sizing: border-box; */
  border: 1px solid #dcdfe6;
  border-radius: 5px;
}
.house-title {
  height: auto;
  margin: 20px;
  padding: 0;
}
.house-title a {
  text-decoration: none;
  font-size: 45px;
  font-weight: 700;
  color: #101d37;
}
.house-title a:hover {
  color: #00ae66;
}
.msg-wrap {
  font-size: 18px;
  font-weight: 700;
  color: #9399a5;
}
.msg-wrap i {
  margin: 0 10px;
}
.house-price {
  margin-bottom: 20px;
  color: #fa5741;
  font-size: 18px;
  font-weight: 700;
}
.house-price em {
  font-size: 30px;
}
</style>



